import router from '../router/index.js'
import axios from 'axios'

const url = {
    load: 'https://images.weserv.nl/?url=',
    server: 'http://localhost:3001/',
}
// 
const debounce = function debounce(func, wait, immediate) {
    //console.log('');
    let timeout; // 
    return function (...args) { // 
        const context = this; // 
        const later = function () { // 
            timeout = null; // 
            if (!immediate) func.apply(context, args); // 
        };
        const callNow = immediate && !timeout; // 
        clearTimeout(timeout); // 
        timeout = setTimeout(later, wait); // 
        if (callNow) func.apply(context, args); // 
    };
}
  // 
const throttle =function(func, wait) {
    let timeout; // 
    return function (...args) { // 
        const context = this; // 
        if (!timeout) { // 
            func.apply(context, args); // 
            timeout = setTimeout(() => {
                timeout = null; // 
            }, wait); // 
        }
    };
  }
const goHome = function(){
    const url = "Home";
    const routeData = router.resolve({ path: url });
    window.open(routeData.href, 'Home')
}
function goBlank(id) {
    const url = '/blank/' + id
    const routeData = router.resolve({ path: url })
    window.open(routeData.href, '_blank')
}

function goDetail(id) {
    const url = '/video/' + id
    const routeData = router.resolve({ path: url })
    window.open(routeData.href, 'video')
}
//
const apiAxios = async (method, urlPath, data = null) => {
    // 
    const headers = { 'Content-Type': 'application/json' };

    //  localStorage  token 
    const userInfo = localStorage.getItem('userInfo');
    // console.log(userInfo);
    // console.log(localStorage)
    if (userInfo) {
        const { token } = JSON.parse(userInfo);
        headers['Authorization'] = `Bearer ${token}`;
    }
    try {
        // 
        const response = await axios({
            method,
            url: `${url.server}${urlPath}`,
            headers,
            data: method === 'POST' ? data : null,
            params: method === 'GET' ? data : null,
        });
        return response.data;
    } catch (error) {
        // 
        console.error('API request failed:', error);
        throw error; // 
    }
};
//get
const get = async (url, params) => {
    return await apiAxios('GET', url, params)
}
//post
const post = async (url, params) => {
    
    return await apiAxios('POST', url, params)
}

const numChange = function(num) {
    if (num >= 10000) {
        var n1 = Math.floor(num / 10000)
        var n2 = Math.floor((num % 10000) / 1000)
        if (n2 == 0) {
            return n1 + ''
        } else {
            return n1 + '.' + n2 + ''
        }
    } else {
        return num
    }
}

export default {
    url,
    goHome,
    get,
    post,
    numChange,
    goBlank,
    goDetail,
    debounce,
    throttle
}